html, body {
  margin:0px;
  height:100%;
  min-height: 175px;
  min-width: 175px;
}


.game {
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0px;
  min-height: 200px;
  background-color: #042c84;
}


.square {
  display: flex;
  float: left;
  align-items: center;
  justify-content: center;
  margin: 0.3%;
  font-size: 1.5em;
  font-weight: bold;
  line-height: 1em;
/*  min-height: 50px;*/
  height: 95%;
  aspect-ratio: 1/1;
  border-radius: 12px;
  background-color: #0086df;
  color: white;
  box-sizing: border-box;
  border: 7px solid #01143d;
  border-color: #01143d;
  user-select: none;
}

.square:hover {
  background-color: #32dee0;
  transition: 0.7s;
}


.board-row:after {
  clear: both;
  content: '';
  display: flex;
}

.game-board {
  border-collapse: collapse;
  margin: auto;
  position: absolute;
  top: 10vh;
  left: 10vw;
  height: 90vh;
  width: 90vw;
/*  min-height: 175px;*/
  min-width: 400px;
  max-width: 75%;
  max-height: 75%;
  aspect-ratio: 1/1;
  white-space: nowrap;
}

.info {
  color: white;
  text-align: center;
  position: absolute;
  font-weight: bold;
  font-size: 24px;
  margin-top: -40px;
}




